.admin-container {
    font-family: Arial, sans-serif;
    padding: 20px;
    max-width: 800px;
    margin: 0 auto;
    background-color: #f9f9f9;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  .admin-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
  }
  
  .admin-title {
    text-align: center;
    font-size: 24px;
    margin-bottom: 20px;
    color: #333;
  }
  
  .admin-success {
    background-color: #4caf50;
    color: #fff;
    padding: 10px;
    border-radius: 5px;
    text-align: center;
    margin-bottom: 20px;
  }
  
  .admin-form {
    margin-bottom: 30px;
  }
  
  .admin-form h2 {
    font-size: 18px;
    margin-bottom: 10px;
  }
  
  .admin-input {
    display: block;
    width: 100%;
    padding: 10px;
    margin-bottom: 10px;
    border-radius: 5px;
    border: 1px solid #ccc;
    font-size: 16px;
  }
  
  .admin-button {
    background-color: #007bff;
    color: #fff;
    padding: 10px 15px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    margin-top: 10px;
  }
  
  .admin-button:hover {
    background-color: #0056b3;
  }
  
  .admin-products {
    margin-top: 20px;
  }
  
  .admin-table {
    width: 100%;
    border-collapse: collapse;
  }
  
  .admin-table th,
  .admin-table td {
    padding: 10px;
    border: 1px solid #ddd;
    text-align: left;
  }
  

  .logout-button {
    padding: 10px 20px;
    background-color: #f44336;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 14px;
    font-weight: bold;
  }
  
  .logout-button:hover {
    background-color: #d32f2f;
  }

  /* Wrapper for the buttons */
.admin-actions {
  display: flex;
  justify-content: space-around; /* Add space between buttons */
  align-items: center;
  gap: 10px; /* Adjust the spacing between buttons */
}

/* Buttons */
.admin-edit-button,
.admin-delete-button {
  background-color: transparent;
  color: #333;
  border: none;
  padding: 5px;
  font-size: 16px;
  cursor: pointer;
  transition: color 0.3s ease;
}

.admin-edit-button:hover {
  color: #4caf50; /* Green hover for edit */
}

.admin-delete-button:hover {
  color: #e74c3c; /* Red hover for delete */
}

/* Icons styling */
.admin-edit-button i,
.admin-delete-button i {
  font-size: 18px;
}
.cancel-button {
  background-color: #f44336;  /* Red background */
  margin-left: 10px;  /* Some spacing */
}
.cancel-button:hover {
  background-color: #d32f2f;  /* Darker red when hovered */
}